import maplibre, { MapMouseEvent } from 'maplibre-gl';

const map = new maplibre.Map( {
    container: 'map',
    style: 'https://api.maptiler.com/maps/basic/style.json?key=get_your_own_OpIi9ZULNHzrESv6T2vL', //'https://demotiles.maplibre.org/style.json', // stylesheet location
    center: [ - 99.12, 19.43 ], // starting position [lng, lat]
    zoom: 12 // starting zoom
} );

window.map = map;

map.on('load', () => {
    map.addSource("stations", {
        "type": "geojson",
        "data": "http://localhost:9080/example/dev-bundle/data/starting_points.geojson"
    });
    map.addLayer({
        "id": "starting_points",
        "type": "circle",
        "source": "stations",
        "paint": {
            "circle-radius": 6,
            "circle-color": "#000000"
        }
    });

    map.on('mousemove', onMouseMove);
});

function onMouseMove(e) {
    const features = map.queryRenderedFeatures(
        [
            [e.point.x - 10, e.point.y - 10],
            [e.point.x + 10, e.point.y + 10],
        ],
        {
            layers: ["starting_points"],
        }
    );

    console.log(features);
}